<template>
  <div class="login-container">
    <div class="con">
      <flat-surface-shader
        class="shader"
        type="canvas"
        :light="{ambient: '#E6A23C', diffuse: '#F56C6C', draw: false}"
        :mesh="{segments: 20, slices:10, width: 1.8, height: 1.8}"
      ></flat-surface-shader>
    </div>
    <el-form
      :model="ruleForm2"
      :rules="rules2"
      status-icon
      ref="ruleForm2"
      label-position="left"
      label-width="0px"
      class="demo-ruleForm login-page"
    >
      <div class="title">后台系统LHC087</div>
      <el-form-item prop="name">
        <el-input
          type="text"
          v-model="ruleForm2.name"
          auto-complete="off"
          placeholder="用户名"
          clearable
        ></el-input>
      </el-form-item>
      <el-form-item prop="password">
        <el-input
          type="password"
          v-model="ruleForm2.password"
          auto-complete="off"
          placeholder="密码"
          clearable
          @keyup.enter.native="handleSubmit"
        ></el-input>
      </el-form-item>
      <el-form-item style="width:100%;">
        <el-button type="primary" style="width:100%;" @click="handleSubmit" :loading="logining">登录</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
// import { mapMutations } from "vuex";
import { postLogin } from "@/api/api";

export default {
  name: "LoGin",
  data() {
    return {
      fromUrl: "/taskpage",
      userToken: "",
      logining: false,
      ruleForm2: {
        name: "",
        password: ""
      },
      rules2: {
        name: [{ required: true, message: "请输入登陆名", trigger: "blur" }],
        password: [{ required: true, message: "请输入密码", trigger: "blur" }]
      }
    };
  },
  methods: {
    handleSubmit(event) {
      var that = this;
      that.$refs.ruleForm2.validate(valid => {
        if (valid) {
          that.logining = true;

          //这里用来根据后台返回来的roleauthorization做侧边栏路由的权限控制显示
          if (
            that.ruleForm2.name == "root" &&
            that.ruleForm2.password == "root"
          ) {
            window.sessionStorage.setItem("handleUserName", "管理员");
            window.sessionStorage.setItem("roleauthorization", 0);
          } else {
            window.sessionStorage.setItem("handleUserName", "其他账号");
            window.sessionStorage.setItem("roleauthorization", 1);
          }
          that.userToken = "这里是后台返回来的token";
          that.$store.commit("set_token", that.userToken);
          window.sessionStorage.setItem("token", that.userToken);
          that.$router.push(that.fromUrl);

          // postLogin({
          //   loginName: that.ruleForm2.name,
          //   password: that.ruleForm2.password
          // })
          //   .then(res => {
          //     that.logining = false;
          //     if (res.code == 200) {
          //       that.$message.success(res.msg);
          //       that.userToken = res.data.token;
          //       //根据store中set_token方法将token保存至localStorage/sessionStorage中，data["Authentication-Token"]，获取token的value值
          //       that.$store.commit("set_token", that.userToken);
          //       window.sessionStorage.setItem("token", that.userToken);
          //       //保存用户名
          //       window.sessionStorage.setItem(
          //         "handleUserName",
          //         res.data.userNick
          //       );
          //       window.sessionStorage.setItem(
          //         "roleauthorization",
          //         res.data.scope
          //       );
          //       that.$router.push(that.fromUrl);
          //     } else {
          //       that.$message.error(res.msg);
          //     }
          //   })
          //   .catch(err => {
          //     that.logining = false;
          //   });
        } else {
          return false;
        }
      });
    }
  }
};
</script>

<style scoped>
.shader {
  width: 100vw;
  height: 100vh;
}
.con {
  position: fixed;
  top: 0px;
  left: 0px;
  z-index: -1;
}
.login-page {
  -webkit-border-radius: 5px;
  border-radius: 5px;
  margin: 25vh auto;
  width: 50vw;
  padding: 35px 35px 15px;
  background: linear-gradient(rgba(64, 158, 255, 0.9), rgba(103, 194, 58, 0.3));
  border: 1px solid #eaeaea;
  box-shadow: 0 0 25px #cac6c6;
}
.title {
  margin: 10px auto;
  width: 100%;
  text-align: center;
  line-height: 30px;
  font-size: 26px;
}
@media (min-width: 769px) {
  .login-page {
    width: 30vw;
  }
}
</style>


