<template>
  <el-container>
    <el-main>
      <div class="demo-input-suffix" style="margin-bottom:30px;">
        <template>
          账号:
          <el-input placeholder="请输入账号" v-model="realname" clearable style="width:160px;"></el-input>
        </template>
        <template>
          部门:
          <el-select
            v-model="deptname"
            placeholder="请选择部门"
            @change="selectgetUsersCentergetAllUserfunction()"
            clearable
          >
            <el-option
              v-for="item in deptlist"
              :key="item.id"
              :label="item.deptName"
              :value="item.id"
            ></el-option>
          </el-select>
        </template>
        <template>
          账号状态:
          <el-select
            v-model="bzType"
            placeholder="请选择结算类型"
            @change="selectgetUsersCentergetAllUserfunction()"
          >
            <el-option
              v-for="item in bzTypelist"
              :key="item.id"
              :label="item.name"
              :value="item.id"
            ></el-option>
          </el-select>
        </template>
        <template>
          时间:
          <el-date-picker
            v-model="selecttime"
            type="datetimerange"
            :picker-options="pickerOptions"
            range-separator="至"
            start-placeholder="开始日期"
            end-placeholder="结束日期"
            value-format="yyyy-MM-dd"
            align="center"
          ></el-date-picker>
        </template>
        <el-button
          type="primary"
          icon="el-icon-search"
          @click="selectgetUsersCentergetAllUserfunction()"
        >搜索</el-button>

        <el-button
          type="primary"
          icon="el-icon-refresh"
          circle
          style="float:right;"
          @click="updataall"
        ></el-button>
      </div>
      <el-table
        :data="tableData"
        border
        style="width: 100%"
        v-loading="loading"
        :default-sort="{prop: 'date', order: 'descending'}"
      >
        <el-table-column label="序号" type="index" width="50" align="center"></el-table-column>
        <el-table-column label="账号" prop="phone" align="center"></el-table-column>
        <el-table-column label="部门" prop="deptName" align="center"></el-table-column>
        <el-table-column label="账号状态" prop="userStatus" align="center">
          <template slot-scope="scope">
            <span v-if="scope.row.userStatus==1">已注册</span>
            <span v-if="scope.row.userStatus==3">已冻结</span>
          </template>
        </el-table-column>
        <el-table-column label="注册时间" prop="createTime" align="center" sortable></el-table-column>
        <el-table-column label="操作" align="center" width="200px;">
          <template slot-scope="scope">
            <el-button size="small" type="primary" @click="setUser(scope.row)">编辑</el-button>
            <el-button
              size="small"
              type="danger"
              @click="unbundLing(scope.row.id,scope.row.userStatus)"
              v-if="scope.row.userStatus===1"
            >冻结</el-button>
            <el-button
              size="small"
              type="warning"
              @click="unbundLing(scope.row.id,scope.row.userStatus)"
              v-if="scope.row.userStatus===3"
            >解绑</el-button>
          </template>
        </el-table-column>
      </el-table>
      <!-- 分页 -->
      <div>
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="currentPage"
          :page-sizes="pageSizes"
          :page-size="PageSize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="totalCount"
        ></el-pagination>
      </div>
    </el-main>
    <el-dialog
      title="修改部门"
      :visible.sync="editDialog"
      @close="resetForm('editForm')"
      align="center"
      :close-on-click-modal="false"
    >
      <el-form :model="editForm" :rules="editRules" ref="editForm" label-width="120px">
        <el-form-item label="序号" prop="id">
          <el-input type="text" v-model.number="editForm.id" :disabled="true"></el-input>
        </el-form-item>
        <el-form-item label="部门" prop="editdepartment">
          <el-select
            v-model="editForm.editdepartment"
            placeholder="请选择部门"
            clearable
            style="width:100%;"
          >
            <el-option
              v-for="item in deptlist"
              :key="item.id"
              :label="item.deptName"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-button type="primary" @click.stop="updateUser('editForm')">修改</el-button>
      </el-form>
    </el-dialog>
  </el-container>
</template>

<script>
import DATA from "./bloggerpagejson.js"; //引入静态数据
import {
  getUsersCentergetAllUser,
  getDeptallTotal,
  postUsersCenterUpdateBozhuDept,
  postUsersCenterUpdate
} from "@/api/api";
export default {
  name: "BlogGerPage",
  inject: ["reload"],

  //组件渲染完毕后调用getUsersCentergetAllUserfunction,来获取所有的用户列表
  mounted: function() {
    //加载数据
    this.tableData = DATA.data.list;
    this.loading = false;
    this.deptlist = DATA.dept;

    // this.getUsersCentergetAllUserfunction();
    // this.getDeptallTotalfunction();
  },
  watch: {},
  data() {
    return {
      deptname: "",
      deptlist: [],
      bzType: 1,
      bzTypelist: [{ id: 1, name: "已注册" }, { id: 3, name: "已冻结" }],
      realname: "",
      loading: true,
      tableData: [],
      // 默认显示第几页
      currentPage: 1,
      // 总条数，根据接口获取数据长度(注意：这里不能为空)
      totalCount: 0,
      // 个数选择器（可修改）
      pageSizes: [15, 20, 30, 40, 50],
      // 默认每页显示的条数（可修改）
      PageSize: 15,
      selecttime: "",
      pickerOptions: {
        shortcuts: [
          {
            text: "最近一周",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit("pick", [start, end]);
            }
          },
          {
            text: "最近一个月",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit("pick", [start, end]);
            }
          },
          {
            text: "最近三个月",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
              picker.$emit("pick", [start, end]);
            }
          }
        ]
      },
      editDialog: false,
      //编辑表单
      editForm: {
        id: "",
        editdepartment: ""
      },
      editRules: {
        editdepartment: [
          { required: true, message: "部门不能为空", trigger: "change" }
        ]
      }
    };
  },
  methods: {
    //初始化默认获取表格数据
    getUsersCentergetAllUserfunction: function(insuserStatus) {
      this.tableData = [];
      if (
        (this.selecttime == "" || this.selecttime == null) &&
        (this.realname == "" || this.realname == null)
      ) {
        getUsersCentergetAllUser({
          deptId: this.deptname,
          userStatus: this.bzType,
          pageNum: this.currentPage,
          pageSize: this.PageSize,
          userRole: 2
        })
          .then(res => {
            if (res.code == 200) {
              if (res.data == undefined) {
                this.$message.info("数据为空!");
              } else {
                this.tableData = res.data.list;
                this.totalCount = res.data.total;
                this.$message.success(res.data.msg);
              }
            } else {
              this.$message.info("暂无数据");
            }
          })
          .catch(err => {});
      } else if (
        (this.selecttime == "" || this.selecttime == null) &&
        this.realname != ""
      ) {
        getUsersCentergetAllUser({
          deptId: this.deptname,
          userStatus: this.bzType,
          phone: this.realname,
          pageNum: this.currentPage,
          pageSize: this.PageSize,
          userRole: 2
        })
          .then(res => {
            if (res.code == 200) {
              if (res.data == undefined) {
                this.$message.info("数据为空!");
              } else {
                this.tableData = res.data.list;
                this.totalCount = res.data.total;
                this.$message.success(res.data.msg);
              }
            } else {
              this.$message.info("暂无数据");
            }
          })
          .catch(err => {});
      } else if (this.selecttime != "" && this.realname == "") {
        getUsersCentergetAllUser({
          deptId: this.deptname,
          userStatus: this.bzType,
          pageNum: this.currentPage,
          pageSize: this.PageSize,
          userRole: 2,
          startTime: this.selecttime[0],
          endTime: this.selecttime[1]
        })
          .then(res => {
            if (res.code == 200) {
              if (res.data == undefined) {
                this.$message.info("数据为空!");
              } else {
                this.tableData = res.data.list;
                this.totalCount = res.data.total;
                this.$message.success(res.data.msg);
              }
            } else {
              this.$message.info("暂无数据");
            }
          })
          .catch(err => {});
      } else {
        getUsersCentergetAllUser({
          deptId: this.deptname,
          userStatus: this.bzType,
          phone: this.realname,
          pageNum: this.currentPage,
          pageSize: this.PageSize,
          userRole: 2,
          startTime: this.selecttime[0],
          endTime: this.selecttime[1]
        })
          .then(res => {
            if (res.code == 200) {
              if (res.data == undefined) {
                this.$message.info("数据为空!");
              } else {
                this.tableData = res.data.list;
                this.totalCount = res.data.total;
                this.$message.success(res.data.msg);
              }
            } else {
              this.$message.info("暂无数据");
            }
          })
          .catch(err => {});
      }
      this.loading = false;
    },
    getDeptallTotalfunction() {
      getDeptallTotal()
        .then(res => {
          if (res.code == 200) {
            if (res.data == undefined) {
              this.$message.info("数据为空!");
            } else {
              this.deptlist = res.data;
              this.$message.success(res.msg);
            }
          } else {
            this.$message.info("暂无数据");
          }
        })
        .catch(err => {});
    },
    //修改表单赋值
    setUser: function(row) {
      //编辑的弹出框开启
      this.editDialog = true;
      this.editForm.id = row.id;
      this.editForm.editdepartment = row.deptId;
    },
    updateUser: function(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          postUsersCenterUpdateBozhuDept({
            userId: this.editForm.id,
            deptId: this.editForm.editdepartment
          })
            .then(res => {
              if (res.code == 200) {
                this.resetForm(formName);
                this.$message.success(res.msg);
                this.getUsersCentergetAllUserfunction();
              } else {
                this.$message.info(res.msg);
              }
            })
            .catch(err => {});
        } else {
          return false;
        }
      });
    },

    unbundLing(ins, insuserStatus) {
      this.$confirm("确定要修改当前账号的状态吗?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
        center: true
      })
        .then(() => {
          let that = this;
          postUsersCenterUpdate({
            userId: ins,
            userStatus: insuserStatus,
            userRole: 2
          })
            .then(res => {
              if (res.code == 200) {
                that
                  .$confirm(res.msg, "提示", {
                    confirmButtonText: "确定",
                    showClose: false,
                    showCancelButton: false,
                    type: "warning",
                    center: true
                  })
                  .then(() => {
                    that.getUsersCentergetAllUserfunction();
                  });
              } else {
                that.$message.info(res.msg);
              }
            })
            .catch(err => {});
        })
        .catch(() => {
          this.$message.info("已取消");
        });
    },

    selectgetUsersCentergetAllUserfunction: function() {
      this.currentPage = 1;
      this.getUsersCentergetAllUserfunction();
    },

    //分页
    // 每页显示的条数
    handleSizeChange(val) {
      // 改变每页显示的条数
      this.PageSize = val;
      // 注意：在改变每页显示的条数时，要将页码显示到第一页
      this.currentPage = 1;
      this.getUsersCentergetAllUserfunction();
    },
    // 显示第几页
    handleCurrentChange(val) {
      // 改变默认的页数
      this.currentPage = val;
      this.getUsersCentergetAllUserfunction();
    },
    resetForm: function(formName) {
      if (formName == "editForm") {
        this.$refs["editForm"].resetFields();
        //编辑的弹出框关掉
        this.editDialog = false;
      }
    },
    updataall() {
      this.reload(); //刷新当前页
    }
  }
};
</script>
<style scoped>
</style>


