<template>
  <el-container>
    <el-main>
      <div class="demo-input-suffix" style="margin-bottom:30px;">
        <el-button type="primary" icon="el-icon-download" @click="export2Excel()">导出</el-button>
        <el-button type="primary" icon="el-icon-back" style="float:right;" @click="callback">返回</el-button>
      </div>
      <el-table :data="tableData" border style="width: 100%" v-loading="loading">
        <el-table-column label="序号" type="index" width="50" align="center"></el-table-column>
        <el-table-column label="订单编号" prop="orderNo" align="center">
          <template slot-scope="scope">
            <a @click="turnrouter(scope.row.orderNo)" class="buttonText">{{scope.row.orderNo}}</a>
          </template>
        </el-table-column>
        <el-table-column label="部门" prop="deptName" align="center"></el-table-column>
        <el-table-column label="昵称" prop="joinNickname" align="center"></el-table-column>
        <el-table-column label="标题" prop="title" align="center">
          <template slot-scope="scope">
            <el-popover trigger="hover" placement="top">
              <span>{{ scope.row.title }}</span>
              <div slot="reference" class="name-wrapper">{{ scope.row.title }}</div>
            </el-popover>
          </template>
        </el-table-column>
        <el-table-column label="任务数量" prop="orderNum" align="center"></el-table-column>
        <el-table-column label="任务要求" prop="taskformStr" align="center"></el-table-column>
        <el-table-column label="单价(元)" prop="orderPrice" align="center"></el-table-column>
      </el-table>
    </el-main>
  </el-container>
</template>

<script>
import DATA from "./settlementdetailspagejson.js"; //引入静态数据
import { postPayCenterPayDetail } from "@/api/api";
export default {
  name: "SettlementDetailsPage",
  props: ["sendid"],
  //组件渲染完毕后调用postPayCenterPayDetailfunction,来获取所有的用户列表
  mounted: function() {
    //加载数据
    this.tableData = DATA.data;
    this.loading = false;

    // this.postPayCenterPayDetailfunction();
  },

  data() {
    return {
      loading: true,
      tableData: []
    };
  },
  methods: {
    //导出excel
    export2Excel() {
      let tableDataexcel = this.tableData;
      for (let i = 0; i < tableDataexcel.length; i++) {
        tableDataexcel[i].index = i + 1;
      }
      require.ensure([], () => {
        let time = new Date();
        let year = time.getFullYear();
        let month = time.getMonth() + 1;
        let day = time.getDate();
        let name = year + "." + month + "." + day;
        const {
          export_json_to_excel
        } = require("./../../excel/Export2Excel.js");
        const tHeader = [
          "序号",
          "订单编号",
          "部门",
          "昵称",
          "标题",
          "任务数量",
          "任务要求",
          "单价（元）"
        ]; // 上面设置Excel的表格第一行的标题
        const filterVal = [
          "index",
          "orderNo",
          "deptName",
          "joinNickname",
          "title",
          "orderNum",
          "taskformStr",
          "orderPrice"
        ]; // 上面的index、phone_Num、school_Name是tableData里对象的属性
        const list = tableDataexcel; //把data里的tableData存到list
        const data = this.formatJson(filterVal, list);
        export_json_to_excel(tHeader, data, name + "结算明细excel列表");
      });
    },
    formatJson(filterVal, jsonData) {
      return jsonData.map(v => filterVal.map(j => v[j]));
    },

    //初始化默认获取表格数据
    postPayCenterPayDetailfunction: function() {
      postPayCenterPayDetail({ orderIdsStr: this.sendid })
        .then(res => {
          if (res.code == 200) {
            if (res.data == undefined) {
              this.$message.info("数据为空!");
            } else {
              this.tableData = res.data;
              this.$message.success(res.msg);
            }
          } else {
            this.$message.info(res.msg);
          }
          this.loading = false;
        })
        .catch(err => {});
    },
    callback() {
      this.$emit("listenToChildEvent", "false");
    },
    //路由跳转
    turnrouter(ins) {
      this.$router.push({
        path: "/orderpage",
        query: {
          orderNo: ins
        }
      });
    }
  }
};
</script>
<style scoped>
.name-wrapper {
  cursor: pointer;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.buttonText {
  text-decoration: none;
  cursor: pointer;
  color: #409eff !important;
}
</style>


