<template>
  <el-container>
    <el-main>
      <div class="demo-input-suffix" style="margin-bottom:30px;">
        <template>
          平台:
          <el-select v-model="rwType" placeholder="请选择平台" @change="selectOrderCenterAllfunction()">
            <el-option
              v-for="item in rwTypelist"
              :key="item.id"
              :label="item.name"
              :value="item.id"
            ></el-option>
          </el-select>
        </template>
        <template>
          任务标题:
          <el-input placeholder="请输入任务标题" v-model="rwtitle" clearable style="width:160px;"></el-input>
        </template>
        <template>
          接单员:
          <el-input placeholder="请输入接单员" v-model="publisher" clearable style="width:160px;"></el-input>
        </template>
        <template>
          订单状态:
          <el-select
            v-model="rwStatus"
            placeholder="请选择订单状态"
            clearable
            @change="selectOrderCenterAllfunction()"
          >
            <el-option
              v-for="item in rwStatuslist"
              :key="item.id"
              :label="item.name"
              :value="item.id"
            ></el-option>
          </el-select>
        </template>
        <template>
          时间:
          <el-date-picker
            v-model="selecttime"
            type="datetimerange"
            :picker-options="pickerOptions"
            range-separator="至"
            start-placeholder="开始日期"
            end-placeholder="结束日期"
            value-format="yyyy-MM-dd"
            align="center"
          ></el-date-picker>
        </template>
        <el-button type="primary" icon="el-icon-search" @click="selectOrderCenterAllfunction()">搜索</el-button>
        <el-button
          type="primary"
          icon="el-icon-refresh"
          circle
          style="float:right;"
          @click="updataall"
        ></el-button>
      </div>
      <el-table
        :data="tableData"
        border
        style="width: 100%"
        v-loading="loading"
        :default-sort="{prop: 'date', order: 'descending'}"
      >
        <el-table-column label="序号" type="index" width="50" align="center"></el-table-column>
        <el-table-column label="订单编号" prop="orderNo,platformUrl" align="center">
          <template slot-scope="scope">
            <a
              :href="scope.row.platformUrl"
              class="buttonText"
              target="_blank"
            >{{scope.row.orderNo}}</a>
          </template>
        </el-table-column>
        <el-table-column label="平台" prop="platform" align="center">
          <template slot-scope="scope">
            <span v-if="scope.row.platform==1">微博</span>
          </template>
        </el-table-column>
        <el-table-column label="任务标题" prop="title" align="center">
          <template slot-scope="scope">
            <el-popover trigger="hover" placement="top">
              <span>{{ scope.row.title }}</span>
              <div slot="reference" class="name-wrapper">
                <a @click="turnrouter(scope.row.taskId)" class="buttonText">{{scope.row.title}}</a>
              </div>
            </el-popover>
          </template>
        </el-table-column>
        <el-table-column label="昵称" prop="nickName" align="center"></el-table-column>
        <el-table-column label="接单员" prop="realName" align="center"></el-table-column>
        <el-table-column label="接单数量" prop="orderNum" align="center"></el-table-column>
        <el-table-column label="订单状态" prop="orderStatus" align="center">
          <template slot-scope="scope">
            <span
              v-if="scope.row.orderStatus==1"
              @click="openshowDialog(scope.row)"
              class="buttonText"
            >已接单</span>
            <span
              v-if="scope.row.orderStatus==2"
              @click="openshowDialog(scope.row)"
              class="buttonText"
            >已完成</span>
            <el-popover trigger="hover" placement="top" v-if="scope.row.orderStatus==3">
              <span>{{ scope.row.customAppealExplain }}</span>
              <div slot="reference" class="buttonText" @click="openshowDialog(scope.row)">系统审核异常</div>
            </el-popover>
            <el-popover trigger="hover" placement="top" v-if="scope.row.orderStatus==4">
              <span>{{ scope.row.appealExplain }}</span>
              <div slot="reference" class="buttonText" @click="openshowDialog(scope.row)">人工审核异常</div>
            </el-popover>
            <span
              v-if="scope.row.orderStatus==5"
              @click="openshowDialog(scope.row)"
              class="buttonText"
            >接单员申诉中</span>
          </template>
        </el-table-column>
        <el-table-column label="支付状态" prop="orderPayStatus" align="center">
          <template slot-scope="scope">
            <span v-if="scope.row.orderPayStatus==0">未结算</span>
            <span v-if="scope.row.orderPayStatus==1">结算中</span>
            <span v-if="scope.row.orderPayStatus==2">已结算</span>
            <span v-if="scope.row.orderPayStatus==3">冻结</span>
            <span v-if="scope.row.orderPayStatus==4">异常</span>
          </template>
        </el-table-column>
        <el-table-column label="部门" prop="deptName" align="center"></el-table-column>
        <el-table-column label="接单时间" prop="startTime" align="center" sortable></el-table-column>
        <el-table-column label="完成时间" prop="submitTime" align="center" sortable></el-table-column>
        <el-table-column
          label="审核时间"
          prop="checkTime"
          align="center"
          v-if="rwStatus==3||rwStatus==4"
          sortable
        ></el-table-column>
        <el-table-column label="申诉时间" prop="appealTime" align="center" v-if="rwStatus==5" sortable></el-table-column>
        <el-table-column label="操作" align="center" width="180px;">
          <template slot-scope="scope">
            <el-button
              size="small"
              type="danger"
              @click="abnormal(scope.row)"
              v-if="scope.row.orderStatus==2&&(scope.row.orderPayStatus==0||scope.row.orderPayStatus==1)"
            >标记为人工异常</el-button>
            <el-button
              size="small"
              type="success"
              @click="abnormal(scope.row)"
              v-if="scope.row.orderStatus==3||scope.row.orderStatus==4"
            >标记为已完成</el-button>
            <el-button
              size="small"
              type="primary"
              @click="auDit(scope.row)"
              v-if="scope.row.orderStatus==5"
            >审核</el-button>
          </template>
        </el-table-column>
      </el-table>

      <!-- 分页 -->
      <div class="tabListPage">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="currentPage"
          :page-sizes="pageSizes"
          :page-size="PageSize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="totalCount"
        ></el-pagination>
      </div>
    </el-main>
    <el-dialog title="记录" :visible.sync="showDialog" align="center" :close-on-click-modal="false">
      <div class="pu">
        <p class="cltitle">接单信息</p>
        <p>接单员:{{jdpeople}}</p>
        <p>接单数量:{{jdnum}}</p>
        <p>接单时间:{{jdtime}}</p>
      </div>
      <div v-if="this.showorderStatus!=1">
        <div class="pu">
          <p class="cltitle">完成信息</p>
          <p>任务要求:{{fntaskformStr}}</p>
          <div class="flexdiv">
            <div class="flexdivch" v-if="fncomments!=''">
              <p>评论</p>
              <ul v-for="(items,index) in fncomments" :key="index">
                <li v-for="(item,indexs) in items" :key="indexs">
                  <p>昵称:{{item.screenName}}</p>
                  <p class="fncom">评论内容:{{item.text}}</p>
                  <p>评论时间:{{item.publishTime}}</p>
                </li>
              </ul>
            </div>
            <div class="flexdivch" v-if="fnlikes!=''">
              <p>点赞</p>
              <ul v-for="(items,index) in fnlikes" :key="index">
                <li v-for="(item,indexs) in items" :key="indexs">
                  <p>昵称:{{item.screenName}}</p>
                  <p>点赞时间:{{item.createdAt}}</p>
                </li>
              </ul>
            </div>
            <div class="flexdivch" v-if="fnforwards!=''">
              <p>转发</p>
              <ul v-for="(items,index) in fnforwards" :key="index">
                <li v-for="(item,indexs) in items" :key="indexs">
                  <p>昵称:{{item.screenName}}</p>
                  <p>转发时间:{{item.createdAt}}</p>
                </li>
              </ul>
            </div>
          </div>
          <p v-if="this.showorderStatus!=3">完成时间:{{fntime}}</p>
        </div>
        <div class="pu" v-if="xtappealExplain!=null">
          <p class="cltitle">系统审核异常信息</p>
          <p>任务未完成:{{xtappealExplain}}</p>
          <p>系统审核异常时间:{{xttime}}</p>
        </div>
        <div class="pu" v-if="rgyx!=''">
          <p class="cltitle">人工审核异常信息记录</p>
          <ul>
            <li v-for="(item,index) in rgyx" :key="index">
              <p>第{{index+1}}次</p>
              <p>审核原因:{{item.content}}</p>
              <p>审核时间:{{item.createTime}}</p>
            </li>
          </ul>
        </div>
        <div v-if="sulist!=''">
          <p class="cltitle">申诉记录</p>
          <div v-for="(items,index) in sulist" :key="index">
            <div v-if="items.role==1" class="listmsg">
              <i class="el-icon-user usericon">
                <span class="user">{{jdpeople}}</span>
              </i>
              <p>申诉图片</p>
              <div id="showimage">
                <div class="showimage">
                  <ul class="imglist" id="imagelist">
                    <li v-for="(item,index) in items.pics" :key="index">
                      <img
                        :src="item.img"
                        referrer="no-referrer|origin|unsafe-url"
                        @click.stop="showbigimg(item.img)"
                      >
                    </li>
                  </ul>
                </div>
              </div>
              <p>申诉描述</p>
              <div id="showcontent">
                <div class="showcontent">{{items.content}}</div>
              </div>
              <p>创建时间:{{items.createTime}}</p>
            </div>
            <div v-if="items.role==2" class="listmsgs">
              <i class="el-icon-user-solid adminicon">
                <span class="admin">{{user_name}}</span>
              </i>
              <p>申诉失败原因</p>
              <div id="showcontent">
                <div class="showcontent">{{items.content}}</div>
              </div>
              <p>创建时间:{{items.createTime}}</p>
            </div>
          </div>
        </div>
      </div>
    </el-dialog>
    <el-dialog
      title="申诉记录"
      :visible.sync="editDialog"
      @close="resetForm('editForm')"
      align="center"
      :before-close="handleClose"
    >
      <div v-for="(items,index) in sulist" :key="index">
        <div v-if="items.role==1" class="listmsg">
          <i class="el-icon-user usericon">
            <span class="user">{{jdpeople}}</span>
          </i>
          <p>申诉图片</p>
          <div id="showimage">
            <div class="showimage">
              <ul class="imglist" id="imagelist">
                <li v-for="(item,index) in items.pics" :key="index">
                  <img
                    :src="item.img"
                    referrer="no-referrer|origin|unsafe-url"
                    @click.stop="showbigimg(item.img)"
                  >
                </li>
              </ul>
            </div>
          </div>
          <p>申诉描述</p>
          <div id="showcontent">
            <div class="showcontent">{{items.content}}</div>
          </div>
          <p>创建时间:{{items.createTime}}</p>
        </div>
        <div v-if="items.role==2" class="listmsgs">
          <i class="el-icon-user-solid adminicon">
            <span class="admin">{{user_name}}</span>
          </i>
          <p>申诉失败原因</p>
          <div id="showcontent">
            <div class="showcontent">{{items.content}}</div>
          </div>
          <p>创建时间:{{items.createTime}}</p>
        </div>
      </div>
      <el-button type="success" :disabled="true" v-if="orderStatus==2">已审核通过</el-button>
      <el-button type="primary" @click.stop="pass()" v-if="orderStatus!=2">通过</el-button>
      <el-button type="warning" @click.stop="nopass()" v-if="orderStatus!=2">不通过</el-button>
    </el-dialog>
    <el-dialog :visible.sync="showimgDialog" align="center" :close-on-click-modal="false">
      <div style="width:50%;height:50%;">
        <img :src="showimg" :alt="showimg" style="width:100%;height:100%;">
      </div>
    </el-dialog>
  </el-container>
</template>

<script>
import DATA from "./orderpagejson.js"; //引入静态数据
import {
  getOrderCenterAll,
  postOrderCenterCompleteToFailure,
  postOrderCenteranyToComplete,
  getOrderCenterOrderItem,
  postOrderCenterAuditFailed
} from "@/api/api";
export default {
  name: "OrderPage",
  inject: ["reload"],

  //组件渲染完毕后调用getOrderCenterAllfunction,来获取所有的用户列表
  mounted: function() {
    //加载数据
    this.tableData = DATA.data.list;
    this.loading = false;

    // this.getOrderCenterAllfunction();
  },
  watch: {},
  data() {
    return {
      user_name: window.sessionStorage.getItem("handleUserName"),
      showDialog: false,
      showorderStatus: "",
      //接单信息
      jdpeople: "",
      jdtime: "",
      jdnum: "",
      //完成信息
      fntaskformStr: "",
      fntime: "",
      fncomments: [],
      fnforwards: [],
      fnlikes: [],
      //系统审核异常信息
      xtappealExplain: "",
      xttime: "",
      //人工审核异常
      rgyx: [],
      //申诉中信息
      // su: "",

      orderStatus: "",
      id: "",
      sulist: [],
      editDialog: false,
      showimgDialog: false,
      showimg: "",

      //表格字段
      loading: true,
      tableData: [],
      // 默认显示第几页
      currentPage: 1,
      // 总条数，根据接口获取数据长度(注意：这里不能为空)
      totalCount: 0,
      // 个数选择器（可修改）
      pageSizes: [15, 20, 30, 40, 50],
      // 默认每页显示的条数（可修改）
      PageSize: 15,
      //搜索框字段
      routeorderNo: this.$route.query.orderNo,
      rwtitle: "",
      publisher: "",
      rwType: 1,
      rwTypelist: [{ id: 1, name: "微博" }],
      rwStatus: "",
      rwStatuslist: [
        { id: 1, name: "已接单" },
        { id: 2, name: "已完成" },
        { id: 3, name: "系统审核异常" },
        { id: 4, name: "人工审核异常" },
        { id: 5, name: "申诉中" }
      ],

      selecttime: "",
      pickerOptions: {
        shortcuts: [
          {
            text: "最近一周",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit("pick", [start, end]);
            }
          },
          {
            text: "最近一个月",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit("pick", [start, end]);
            }
          },
          {
            text: "最近三个月",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
              picker.$emit("pick", [start, end]);
            }
          }
        ]
      }
    };
  },
  methods: {
    showbigimg(ins) {
      this.showimgDialog = true;
      this.showimg = ins;
    },
    //初始化默认获取表格数据
    getOrderCenterAllfunction: function() {
      this.tableData = [];
      if (this.selecttime == "" || this.selecttime == null) {
        getOrderCenterAll({
          pageNum: this.currentPage,
          pageSize: this.PageSize,
          startTime: "",
          endTime: "",
          orderStatus: this.rwStatus,
          realName: this.publisher,
          title: this.rwtitle,
          platform: this.rwType,
          orderNo: this.routeorderNo
        })
          .then(res => {
            if (res.code == 200) {
              if (res.data == undefined) {
                this.$message.info("数据为空!");
              } else {
                this.tableData = res.data.list;
                this.totalCount = res.data.total;
                this.$message.success(res.msg);
              }
            } else {
              this.$message.error(res.msg);
            }
            this.loading = false;
          })
          .catch(err => {});
      } else {
        getOrderCenterAll({
          pageNum: this.currentPage,
          pageSize: this.PageSize,
          startTime: this.selecttime[0],
          endTime: this.selecttime[1],
          orderStatus: this.rwStatus,
          realName: this.publisher,
          title: this.rwtitle,
          platform: this.rwType,
          orderNo: this.routeorderNo
        })
          .then(res => {
            if (res.code == 200) {
              if (res.data == undefined) {
                this.$message.info("数据为空!");
              } else {
                this.tableData = res.data.list;
                this.totalCount = res.data.total;
                this.$message.success(res.msg);
              }
            } else {
              this.$message.error(res.msg);
            }
            this.loading = false;
          })
          .catch(err => {});
      }
    },
    selectOrderCenterAllfunction: function(ins) {
      this.currentPage = 1;
      this.getOrderCenterAllfunction();
    },

    //分页
    // 每页显示的条数
    handleSizeChange(val) {
      // 改变每页显示的条数
      this.PageSize = val;
      // 注意：在改变每页显示的条数时，要将页码显示到第一页
      this.currentPage = 1;
      this.getOrderCenterAllfunction();
    },
    // 显示第几页
    handleCurrentChange(val) {
      // 改变默认的页数
      this.currentPage = val;
      this.getOrderCenterAllfunction();
    },
    updataall() {
      this.reload(); //刷新当前页
      this.$route.query.orderNo = "";
    },
    //异常信息
    abnormal: function(row) {
      if (row.orderStatus == 2) {
        this.$prompt("请输入异常原因", "提示", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          inputPattern: /^[\s\S]*.*[^\s][\s\S]*$/,
          inputErrorMessage: "原因不能为空！",
          center: true
        })
          .then(({ value }) => {
            postOrderCenterCompleteToFailure({
              orderId: row.id,
              content: value
            })
              .then(res => {
                if (res.code == 200) {
                  this.$message.success(res.msg);
                  this.getOrderCenterAllfunction();
                } else {
                  this.$message.info(res.msg);
                }
              })
              .catch(err => {});
          })
          .catch(() => {
            this.$message.info("已取消");
          });
      } else {
        this.$confirm("确定修改该条订单的状态吗?", "提示", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning",
          center: true
        })
          .then(() => {
            postOrderCenteranyToComplete({
              orderId: row.id
            })
              .then(res => {
                if (res.code == 200) {
                  this.$message.success(res.msg);
                  this.getOrderCenterAllfunction();
                } else {
                  this.$message.info(res.msg);
                }
              })
              .catch(err => {});
          })
          .catch(() => {
            this.$message.info("已取消");
          });
      }
    },
    handleClose(done) {
      this.$confirm("确认关闭？")
        .then(_ => {
          done();
        })
        .catch(_ => {});
    },
    openshowDialog: function(row) {
      //编辑的弹出框开启
      this.showDialog = true;
      this.showorderStatus = row.orderStatus;
      getOrderCenterOrderItem({
        orderId: row.id
      })
        .then(res => {
          if (res.code == 200) {
            if (res.data != "") {
              //接单信息
              this.jdpeople = res.data.user.realName;
              this.jdtime = res.data.userOrder.startTime;
              this.jdnum = res.data.userOrder.orderNum;
              //完成信息
              this.fntaskformStr = res.data.task.taskformStr;
              this.fntime = res.data.userOrder.submitTime;
              this.fncomments = res.data.doTaskItem.comments;
              this.fnforwards = res.data.doTaskItem.forwards;
              this.fnlikes = res.data.doTaskItem.likes;
              //系统异常
              this.xtappealExplain = res.data.userOrder.appealExplain;
              this.xttime = res.data.userOrder.checkTime;
              //人工审核异常
              this.rgyx = res.data.userAudits;

              this.sulist = res.data.appealDetails;
              for (var i = 0; i < this.sulist.length; i++) {
                if (this.sulist[i].pics != null) {
                  var b = this.sulist[i].pics.replace(/\[/g, ""); //去除左中括号
                  var c = b.replace(/\]/g, ""); //去除右中括号
                  var d = c.replace(/\ /g, ""); //去除空格
                  var e = d.split(","); //,分割
                  var all = [];
                  for (var j = 0; j < e.length; j++) {
                    all.push({ img: e[j] }); //循环重组list集合
                  }
                  this.sulist[i].pics = all;
                }
              }
            }
          } else {
            this.$message.error(res.msg);
          }
        })
        .catch(err => {});
    },
    //修改表单赋值
    auDit: function(row) {
      //编辑的弹出框开启
      this.editDialog = true;
      this.sulist = [];
      this.jdpeople = row.realName;
      this.orderStatus = row.orderStatus;
      this.id = row.id;
      getOrderCenterOrderItem({
        orderId: row.id
      })
        .then(res => {
          if (res.code == 200) {
            if (res.data != "") {
              this.sulist = res.data.appealDetails;
              for (var i = 0; i < this.sulist.length; i++) {
                if (this.sulist[i].pics != null) {
                  var b = this.sulist[i].pics.replace(/\[/g, ""); //去除左中括号
                  var c = b.replace(/\]/g, ""); //去除右中括号
                  var d = c.replace(/\ /g, ""); //去除空格
                  var e = d.split(","); //,分割
                  var all = [];
                  for (var j = 0; j < e.length; j++) {
                    all.push({ img: e[j] }); //循环重组list集合
                  }
                  this.sulist[i].pics = all;
                }
              }
            }
          } else {
            this.$message.error(res.msg);
          }
        })
        .catch(err => {});
    },

    pass: function() {
      postOrderCenteranyToComplete({
        orderId: this.id
      })
        .then(res => {
          if (res.code == 200) {
            this.$message.success(res.msg);
            this.getOrderCenterAllfunction();
            this.resetForm("editForm");
          } else {
            this.$message.info(res.msg);
          }
        })
        .catch(err => {});
    },

    nopass: function() {
      this.$prompt("请输入不通过原因", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        inputPattern: /^[\s\S]*.*[^\s][\s\S]*$/,
        inputErrorMessage: "原因不能为空！",
        center: true
      })
        .then(({ value }) => {
          postOrderCenterAuditFailed({
            orderId: this.id,
            content: value
          })
            .then(res => {
              if (res.code == 200) {
                this.$message.success(res.msg);
                this.getOrderCenterAllfunction();
                this.resetForm("editForm");
              } else {
                this.$message.info(res.msg);
              }
            })
            .catch(err => {});
        })
        .catch(() => {
          this.$message.info("已取消");
        });
    },
    resetForm: function(formName) {
      if (formName == "editForm") {
        //编辑的弹出框关掉
        this.editDialog = false;
      }
    },

    //路由跳转
    turnrouter(ins) {
      this.$router.push({
        path: "/taskpage",
        query: {
          taskId: ins
        }
      });
    }
  }
};
</script>
<style scoped>
.tabListPage {
  display: inline-block;
}
.buttonText {
  text-decoration: none;
  cursor: pointer;
  color: #409eff !important;
}
.name-wrapper {
  cursor: pointer;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.eldialog {
  text-align: center;
}
.upload-demo {
  text-align: center;
}
.dialog-footer {
  text-align: center;
}
.showimage {
  width: 100%;
  margin: 10px 0px;
}
p {
  padding: 0px 15px;
}
.showcontent {
  margin: 5px auto;
  padding: 5px 15px;
}
.listmsg {
  padding: 5px;
  margin: 5px 0px;
  text-align: left;
  background: #ebeef5;
  width: 80%;
}
.listmsgs {
  padding: 5px;
  margin: 5px 0px;
  text-align: right;
  background: #dcdfe6;
  width: 80%;
}
.usericon {
  position: absolute;
  left: 4%;
  font-size: 36px;
}
.user {
  position: absolute;
  width: 70px;
  font-size: 16px;
  top: 100%;
  text-overflow: -o-ellipsis-lastline;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical;
}
.adminicon {
  position: absolute;
  right: 4%;
  font-size: 36px;
}
.admin {
  position: absolute;
  width: 70px;
  right: -20%;
  font-size: 16px;
  top: 100%;
  text-overflow: -o-ellipsis-lastline;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical;
}
.pu {
  background: #ebeef5;
  padding: 5px;
  margin: 5px 0px;
  width: 80%;
}
.cltitle {
  padding: 0px 15px;
  font-size: 18px;
  color: black;
}
.flexdiv {
  display: flex;
  flex-direction: row;
}
.flexdivch {
  width: 32%;
  color: #409eff;
  background: #dcdfe6;
  flex: 1;
  margin: 0px 2px;
}
.flexdivch ul {
  padding: 2px;
}
.flexdivch ul li {
  text-align: left;
  padding: 0px 5px;
}
.fncom {
  text-overflow: -o-ellipsis-lastline;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
}
ul {
  padding: 0px;
}
ul li {
  list-style-type: none;
}
ul.imglist {
  margin: 0 auto;
  display: inline-block;
}
ul.imglist li {
  float: left;
  padding: 4px 8px;
  width: 200px;
  height: 200px;
  list-style-type: none;
}
ul.imglist li img {
  object-fit: cover;
  display: block;
  width: 100%;
  height: 100%;
}

#imagelist li:hover img {
  transform: scale(2);
}
</style>


