// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import store from './store'
import router from './router'


Vue.config.productionTip = false

import FlatSurfaceShader from 'vue-flat-surface-shader' //登录页面的岩壁效果
Vue.use(FlatSurfaceShader)

import ElementUI from 'element-ui'; //引入Element组件
import 'element-ui/lib/theme-chalk/index.css';
Vue.use(ElementUI);

import '@/styles/index.scss' // global css

import VueScroll from 'vuescroll' //滚动组件
Vue.use(VueScroll, {
  ops: {
    bar: {
      background: '#C0C4CC'
    }
  }
})

import VueWechatTitle from 'vue-wechat-title' //动态页面标题组件
Vue.use(VueWechatTitle)

import 'xe-utils' //vxe-table组件
import VXETable from 'vxe-table'
import 'vxe-table/lib/index.css'
Vue.use(VXETable)

import Blob from '@/excel/Blob' //导出excel
import Export2Excel from '@/excel/Export2Excel.js'




/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  components: {
    App
  },
  template: '<App/>'
})



