<template>
  <el-container>
    <el-main v-if="homeindex==true">
      <div class="demo-input-suffix" style="margin-bottom:30px;">
        <template>
          部门:
          <el-select
            v-model="deptname"
            placeholder="请选择部门"
            @change="selectPayCenterPayListfunction()"
            clearable
          >
            <el-option
              v-for="item in deptlist"
              :key="item.id"
              :label="item.deptName"
              :value="item.id"
            ></el-option>
          </el-select>
        </template>
        <template>
          真名:
          <el-input placeholder="请输入真实姓名" v-model="realname" clearable style="width:160px;"></el-input>
        </template>
        <template>
          结算类型:
          <el-select
            v-model="ddType"
            placeholder="请选择结算类型"
            @change="selectPayCenterPayListfunction()"
          >
            <el-option
              v-for="item in ddTypelist"
              :key="item.id"
              :label="item.name"
              :value="item.id"
            ></el-option>
          </el-select>
        </template>
        <template>
          时间:
          <el-date-picker
            v-model="selecttime"
            type="datetimerange"
            :picker-options="pickerOptions"
            range-separator="至"
            start-placeholder="开始日期"
            end-placeholder="结束日期"
            value-format="yyyy-MM-dd"
            align="center"
          ></el-date-picker>
        </template>
        <el-button type="primary" icon="el-icon-search" @click="selectPayCenterPayListfunction()">搜索</el-button>
        <el-button
          type="primary"
          icon="el-icon-download"
          @click="export2Excel()"
          :disabled="daochushow"
        >导出</el-button>
        <el-button
          type="primary"
          icon="el-icon-refresh"
          circle
          style="float:right;"
          @click="updataall"
        ></el-button>
      </div>
      <el-table
        :data="tableData.slice((currentPage-1)*PageSize,currentPage*PageSize)"
        border
        style="width: 100%"
        v-loading="loading"
        @selection-change="handleSelectionChange"
        ref="multipleTable"
      >
        <el-table-column type="selection" width="55" v-if="isshow==true"></el-table-column>
        <el-table-column label="序号" type="index" width="50" align="center"></el-table-column>
        <el-table-column label="接单员ID" prop="userId" align="center"></el-table-column>
        <el-table-column label="支付宝账号" prop="alipay" align="center"></el-table-column>
        <el-table-column label="真实姓名" prop="realName" align="center"></el-table-column>
        <el-table-column label="任务总数量" prop="childOrderNum" align="center">
          <template slot-scope="scope">
            <el-popover placement="top" trigger="hover" content="点击可查看详细任务。">
              <el-button
                type="text"
                @click="seeMore(scope.row)"
                slot="reference"
              >{{scope.row.childOrderNum}}</el-button>
            </el-popover>
          </template>
        </el-table-column>
        <el-table-column label="总金额" prop="totalMoney" align="center"></el-table-column>
        <el-table-column label="结算状态" prop="payStatusStr" align="center"></el-table-column>
        <el-table-column label="操作" align="center">
          <template slot-scope="scope">
            <el-button
              type="primary"
              size="small"
              @click="settlementingfunction(scope.row.orderIdsStr,scope.row.payStatus)"
              v-if="scope.row.payStatus===0"
            >结算</el-button>
            <el-button
              type="warning"
              size="small"
              @click="settlementingfunction(scope.row.orderIdsStr,scope.row.payStatus)"
              v-if="scope.row.payStatus===1"
            >确定结算</el-button>
          </template>
        </el-table-column>
      </el-table>
      <div v-if="isshowss===0" style="margin:10px 0px;display:inline-block;">
        <el-button @click="settlementingallfunction()" type="primary">选中结算</el-button>
        <el-button @click="toggleSelection()" type="info">取消选择</el-button>
      </div>
      <div v-if="isshowss===1" style="margin:10px 0px;display:inline-block;">
        <el-button @click="settlementingallfunction()" type="warning">选中确定结算</el-button>
        <el-button @click="toggleSelection()" type="info">取消选择</el-button>
      </div>
      <!-- 分页 -->
      <div class="tabListPage">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="currentPage"
          :page-sizes="pageSizes"
          :page-size="PageSize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="totalCount"
        ></el-pagination>
      </div>
    </el-main>
    <SettlementDetailsPage
      :sendid="xquserId"
      v-if="showxq==true"
      v-on:listenToChildEvent="showMsgFormChild"
    ></SettlementDetailsPage>
  </el-container>
</template>


<script>
import DATA from "./settlementpagejson.js"; //引入静态数据
import SettlementDetailsPage from "./SettlementDetailsPage";
import {
  getPayCenterPayList,
  getDeptallTotal,
  postPayCenterSettlement
} from "@/api/api";
export default {
  name: "SettlementPage",
  inject: ["reload"],
  components: {
    SettlementDetailsPage
  },

  //组件渲染完毕后调用getPayCenterPayListfunction,来获取所有的用户列表
  mounted: function() {
    //加载数据
    this.tableData = DATA.data;
    this.loading = false;
    this.deptlist = DATA.dept;

    // this.getPayCenterPayListfunction();
    // this.getDeptallTotalfunction();
  },
  watch: {
    tableData() {
      if (this.tableData == "" || this.tableData == null) {
        this.daochushow = true;
      } else {
        this.daochushow = false;
      }
    }
  },
  data() {
    return {
      daochushow: true,
      xquserId: "",
      showxq: false,
      homeindex: true,
      deptname: "",
      deptlist: [],
      ddType: 0,
      ddTypelist: [
        { id: 0, name: "未结算" },
        { id: 1, name: "结算中" },
        { id: 2, name: "已结算" }
      ],
      isshowss: 0,
      isshow: true,
      multipleSelection: "",
      realname: "",
      loading: true,
      tableData: [],
      // 默认显示第几页
      currentPage: 1,
      // 总条数，根据接口获取数据长度(注意：这里不能为空)
      totalCount: 0,
      // 个数选择器（可修改）
      pageSizes: [15, 20, 30, 40, 50],
      // 默认每页显示的条数（可修改）
      PageSize: 15,
      selecttime: "",
      pickerOptions: {
        shortcuts: [
          {
            text: "最近一周",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit("pick", [start, end]);
            }
          },
          {
            text: "最近一个月",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit("pick", [start, end]);
            }
          },
          {
            text: "最近三个月",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
              picker.$emit("pick", [start, end]);
            }
          }
        ]
      }
    };
  },
  methods: {
    showMsgFormChild(data) {
      //与上面子组件v-on事件名相同
      this.showxq = data; //打印子组件传过来的值
      this.homeindex = true;
    },
    getDeptallTotalfunction() {
      getDeptallTotal()
        .then(res => {
          if (res.code == 200) {
            if (res.data == undefined) {
              this.$message.info("数据为空!");
            } else {
              this.deptlist = res.data;
              this.$message.success(res.msg);
            }
          } else {
            this.$message.info("暂无数据");
          }
        })
        .catch(err => {});
    },

    //初始化默认获取表格数据
    getPayCenterPayListfunction: function(inspayStatus) {
      this.tableData = [];
      if (this.selecttime == "" || this.selecttime == null) {
        getPayCenterPayList({
          deptId: this.deptname,
          payStatus: this.ddType,
          realName: this.realname,
          startTime: "",
          endTime: ""
        })
          .then(res => {
            if (res.code == 200) {
              if (res.data == undefined) {
                this.$message.info("数据为空!");
              } else {
                this.tableData = res.data;
                this.totalCount = res.data.length;
                this.$message.success(res.msg);
              }
            } else {
              this.$message.info(res.msg);
            }
          })
          .catch(err => {});
      } else {
        getPayCenterPayList({
          deptId: this.deptname,
          payStatus: this.ddType,
          realName: this.realname,
          startTime: this.selecttime[0],
          endTime: this.selecttime[1]
        })
          .then(res => {
            if (res.code == 200) {
              if (res.data == undefined) {
                this.$message.info("数据为空!");
              } else {
                this.tableData = res.data;
                this.totalCount = res.data.length;
                this.$message.success(res.msg);
              }
            } else {
              this.$message.info(res.msg);
            }
          })
          .catch(err => {});
      }
      this.loading = false;
    },

    toggleSelection() {
      this.$refs.multipleTable.clearSelection();
    },

    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    settlementingfunction(ins, inspayStatus) {
      this.$confirm("结算当前的选中项吗?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
        center: true
      })
        .then(() => {
          let that = this;
          postPayCenterSettlement({
            orderIdsStr: ins,
            payStatus: inspayStatus
          })
            .then(res => {
              if (res.code == 200) {
                that
                  .$confirm(res.msg, "提示", {
                    confirmButtonText: "确定",
                    showClose: false,
                    showCancelButton: false,
                    type: "warning",
                    center: true
                  })
                  .then(() => {
                    that.getPayCenterPayListfunction();
                  })
                  .catch(() => {});
              } else {
                that.$message.error(res.msg);
              }
            })
            .catch(err => {});
        })
        .catch(() => {
          this.$message.info("已取消");
        });
    },
    settlementingallfunction() {
      if (this.multipleSelection.length != 0) {
        this.$confirm("结算当前的选中项吗?", "提示", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning",
          center: true
        })
          .then(() => {
            let orderId = [];
            for (let i = 0; i < this.multipleSelection.length; i++) {
              orderId = orderId.concat(this.multipleSelection[i].orderIds);
            }
            let that = this;
            postPayCenterSettlement({
              orderIdsStr: "[" + orderId + "]",
              payStatus: that.ddType
            })
              .then(res => {
                if (res.code == 200) {
                  that
                    .$confirm(res.msg, "提示", {
                      confirmButtonText: "确定",
                      showClose: false,
                      showCancelButton: false,
                      type: "warning",
                      center: true
                    })
                    .then(() => {
                      that.getPayCenterPayListfunction();
                    })
                    .catch(() => {});
                } else {
                  that.$message.error(res.msg);
                }
              })
              .catch(err => {});
          })
          .catch(() => {
            this.$message.info("已取消");
          });
      } else {
        this.$message.error("勾选不能为空！");
      }
    },
    selectPayCenterPayListfunction: function() {
      this.currentPage = 1;
      if (this.ddType === 0 || this.ddType === 1) {
        this.isshow = true;
      } else {
        this.isshow = false;
      }
      this.isshowss = this.ddType;
      this.getPayCenterPayListfunction();
    },

    //分页
    // 每页显示的条数
    handleSizeChange(val) {
      // 改变每页显示的条数
      this.PageSize = val;
      // 注意：在改变每页显示的条数时，要将页码显示到第一页
      this.currentPage = 1;
    },
    // 显示第几页
    handleCurrentChange(val) {
      // 改变默认的页数
      this.currentPage = val;
    },
    //导出excel
    export2Excel() {
      let geturl = "";
      if (this.selecttime == "" || this.selecttime == null) {
        this.$message.info("请选择时间再导出数据表");
      } else {
        geturl =
          "?payStatus=" +
          this.ddType +
          "&realName=" +
          this.realname +
          "&startTime=" +
          this.selecttime[0] +
          "&endTime=" +
          this.selecttime[1];
        this.$message.info("开始导出表格");
        let url = window.g.apiUrl + "/payCenter/getExcel" + geturl;
        let iframe = document.createElement("iframe");
        iframe.style.display = "none";
        iframe.src = url;
        iframe.onload = function() {
          document.body.removeChild(iframe);
        };
        document.body.appendChild(iframe);
      }
    },

    seeMore: function(row) {
      this.showxq = true;
      this.homeindex = false;
      this.xquserId = row.orderIdsStr;
    },

    updataall() {
      this.reload(); //刷新当前页
    }
  }
};
</script>
<style scoped>
.tabListPage {
  display: inline-block;
}
.name-wrapper {
  cursor: pointer;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.buttonText {
  text-decoration: none;
  cursor: pointer;
  color: #409eff !important;
}
</style>


