<template>
  <el-container>
    <el-main>
      <div class="demo-input-suffix" style="margin-bottom:30px;">
        <template>
          平台:
          <el-select v-model="rwType" placeholder="请选择平台" @change="selectTaskCenterAllfunction()">
            <el-option
              v-for="item in rwTypelist"
              :key="item.id"
              :label="item.name"
              :value="item.id"
            ></el-option>
          </el-select>
        </template>
        <template>
          任务标题:
          <el-input placeholder="请输入任务标题" v-model="rwtitle" clearable style="width:160px;"></el-input>
        </template>
        <template>
          任务状态:
          <el-select
            v-model="rwStatus"
            placeholder="请选择任务状态"
            clearable
            @change="selectTaskCenterAllfunction()"
          >
            <el-option
              v-for="item in rwStatuslist"
              :key="item.id"
              :label="item.name"
              :value="item.id"
            ></el-option>
          </el-select>
        </template>
        <template>
          发布者:
          <el-input placeholder="请输入发布者" v-model="publisher" clearable style="width:160px;"></el-input>
        </template>
        <template>
          时间:
          <el-date-picker
            v-model="selecttime"
            type="datetimerange"
            :picker-options="pickerOptions"
            range-separator="至"
            start-placeholder="开始日期"
            end-placeholder="结束日期"
            value-format="yyyy-MM-dd"
            align="center"
          ></el-date-picker>
        </template>
        <el-button type="primary" icon="el-icon-search" @click="selectTaskCenterAllfunction()">搜索</el-button>
        <el-button
          type="primary"
          icon="el-icon-refresh"
          circle
          style="float:right;"
          @click="updataall"
        ></el-button>
      </div>
      <el-table
        :data="tableData"
        border
        style="width: 100%"
        v-loading="loading"
        :default-sort="{prop: 'date', order: 'descending'}"
      >
        <el-table-column label="序号" type="index" width="50" align="center"></el-table-column>
        <el-table-column label="任务编号" prop="taskNo" align="center"></el-table-column>
        <el-table-column label="平台" prop="platform" align="center">
          <template slot-scope="scope">
            <span v-if="scope.row.platform==1">微博</span>
          </template>
        </el-table-column>
        <el-table-column label="任务标题" prop="title" align="center">
          <template slot-scope="scope">
            <el-popover trigger="hover" placement="top">
              <span>{{ scope.row.title }}</span>
              <div slot="reference" class="name-wrapper buttonText">{{ scope.row.title }}</div>
            </el-popover>
          </template>
        </el-table-column>
        <el-table-column label="任务要求" prop="taskformStr" align="center"></el-table-column>
        <el-table-column label="任务单价" prop="price" align="center"></el-table-column>
        <el-table-column label="任务数量" prop="totalNum" align="center"></el-table-column>
        <el-table-column label="接单数量" prop="taskQuantity" align="center"></el-table-column>
        <el-table-column label="任务状态" prop="taskStatus" align="center">
          <template slot-scope="scope">
            <span v-if="scope.row.taskStatus==1">发布中</span>
            <span v-if="scope.row.taskStatus==2">已完成</span>
            <span v-if="scope.row.taskStatus==3">已撤单</span>
            <span v-if="scope.row.taskStatus==4">已结束</span>
          </template>
        </el-table-column>
        <el-table-column label="发布者" prop="phone" align="center"></el-table-column>
        <el-table-column label="部门" prop="deptName" align="center"></el-table-column>
        <el-table-column label="发布时间" prop="publishTime" align="center" sortable></el-table-column>
        <el-table-column label="完成时间" prop="cancelTime" align="center" v-if="rwStatus==2" sortable></el-table-column>
        <el-table-column label="撤单时间" prop="cancelTime" align="center" v-if="rwStatus==3" sortable></el-table-column>
        <el-table-column label="结束时间" prop="endTime" align="center" sortable></el-table-column>
      </el-table>
      <!-- 分页 -->
      <div>
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="currentPage"
          :page-sizes="pageSizes"
          :page-size="PageSize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="totalCount"
        ></el-pagination>
      </div>
    </el-main>
  </el-container>
</template>

<script>
import DATA from "./taskpagejson.js"; //引入静态数据
import { getTaskCenterAll } from "@/api/api";
export default {
  name: "TaskPage",
  inject: ["reload"],
  mounted: function() {
    //加载数据
    this.tableData = DATA.data.list;
    this.loading = false;

    //this.getTaskCenterAllfunction();
  },
  watch: {},
  data() {
    return {
      //表格字段
      loading: true,
      tableData: [],
      // 默认显示第几页
      currentPage: 1,
      // 总条数，根据接口获取数据长度(注意：这里不能为空)
      totalCount: 0,
      // 个数选择器（可修改）
      pageSizes: [15, 20, 30, 40, 50],
      // 默认每页显示的条数（可修改）
      PageSize: 15,
      //搜索框字段
      taskId: this.$route.query.taskId,
      rwtitle: "",
      publisher: "",
      rwType: 1,
      rwTypelist: [{ id: 1, name: "微博" }],
      rwStatus: "",
      rwStatuslist: [
        { id: 1, name: "发布中" },
        { id: 2, name: "已完成" },
        { id: 3, name: "已撤单" },
        { id: 4, name: "已结束" }
      ],
      selecttime: "",
      pickerOptions: {
        shortcuts: [
          {
            text: "最近一周",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit("pick", [start, end]);
            }
          },
          {
            text: "最近一个月",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit("pick", [start, end]);
            }
          },
          {
            text: "最近三个月",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
              picker.$emit("pick", [start, end]);
            }
          }
        ]
      }
    };
  },
  methods: {
    //初始化默认获取表格数据
    getTaskCenterAllfunction: function() {
      this.tableData = [];
      if (this.selecttime == "" || this.selecttime == null) {
        getTaskCenterAll({
          taskId: this.taskId,
          pageNum: this.currentPage,
          pageSize: this.PageSize,
          startTime: "",
          endTime: "",
          taskStatus: this.rwStatus,
          nickName: this.publisher,
          title: this.rwtitle,
          platform: this.rwType
        })
          .then(res => {
            if (res.code == 200) {
              if (res.data == undefined) {
                this.$message.info("数据为空!");
              } else {
                this.tableData = res.data.list;
                this.totalCount = res.data.total;
                this.$message.success(res.msg);
              }
            } else {
              this.$message.info("暂无数据");
            }
            this.loading = false;
          })
          .catch(err => {});
      } else {
        getTaskCenterAll({
          taskId: this.taskId,
          pageNum: this.currentPage,
          pageSize: this.PageSize,
          startTime: this.selecttime[0],
          endTime: this.selecttime[1],
          taskStatus: this.rwStatus,
          nickName: this.publisher,
          title: this.rwtitle,
          platform: this.rwType
        })
          .then(res => {
            if (res.code == 200) {
              if (res.data == undefined) {
                this.$message.info("数据为空!");
              } else {
                this.tableData = res.data.list;
                this.totalCount = res.data.total;
                this.$message.success(res.msg);
              }
            } else {
              this.$message.info("暂无数据");
            }
            this.loading = false;
          })
          .catch(err => {});
      }
    },
    selectTaskCenterAllfunction: function(ins) {
      this.currentPage = 1;
      this.getTaskCenterAllfunction();
    },
    //分页
    // 每页显示的条数
    handleSizeChange(val) {
      // 改变每页显示的条数
      this.PageSize = val;
      // 注意：在改变每页显示的条数时，要将页码显示到第一页
      this.currentPage = 1;
      this.getTaskCenterAllfunction();
    },
    // 显示第几页
    handleCurrentChange(val) {
      // 改变默认的页数
      this.currentPage = val;
      this.getTaskCenterAllfunction();
    },
    updataall() {
      this.reload(); //刷新当前页
      this.$route.query.taskId = "";
    }
  }
};
</script>
<style scoped>
.name-wrapper {
  cursor: pointer;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.buttonText {
  text-decoration: none;
  cursor: pointer;
  color: #409eff !important;
}
</style>


