import Vue from 'vue'
import Router from 'vue-router'
import store from '../store/index'
import NProgress from 'nprogress'
import 'nprogress/nprogress.css'
import LoGin from '@/pages/login/LoGin'
import TheLayout from '@/pages/layout/TheLayout'

import TaskPage from '@/pages/task/TaskPage'
import OrderPage from '@/pages/order/OrderPage'
import BlogGerpage from '@/pages/blogger/BlogGerpage'
import SettlementPage from '@/pages/settlement/SettlementPage'
import UserPage from '@/pages/user/UserPage'
import ActivationCodePage from '@/pages/setting/ActivationCodePage'
import DepartmentPage from '@/pages/department/DepartmentPage'


import AppError401 from '@/pages/error/AppError401'
import AppError404 from '@/pages/error/AppError404'

Vue.use(Router)
/* 静态页面路由 */
const routes = [{
    path: '/',
    name: '登录',
    component: LoGin,
    meta: {
      auth: false
    }
  },
  {
    path: '/',
    component: TheLayout,
    icon: 'el-icon-s-home',
    menu: true,
    children: [{
      path: '/taskpage',
      name: '任务中心',
      component: TaskPage,
      meta: {
        requiresAuth: true,
        title: '任务中心'
      },
    }]
  },
  {
    path: '/',
    component: TheLayout,
    icon: 'el-icon-star-on',
    menu: true,
    children: [{
      path: '/orderpage',
      name: '订单中心',
      component: OrderPage,
      meta: {
        requiresAuth: true,
        title: '订单中心'
      },
    }]
  },
  {
    path: '/',
    component: TheLayout,
    icon: 'el-icon-star-on',
    menu: true,
    children: [{
      path: '/settlementpage',
      name: '结算中心',
      component: SettlementPage,
      meta: {
        requiresAuth: true,
        title: '结算中心'
      },
    }]
  },
  {
    path: '/',
    component: TheLayout,
    name: '用户中心',
    icon: 'el-icon-star-on',
    menu: true,
    children: [{
      path: '/userpage',
      name: '接单员',
      component: UserPage,
      meta: {
        requiresAuth: true,
        title: '接单员'
      }
    }, {
      path: '/bloggerpage',
      name: '下单员',
      component: BlogGerpage,
      meta: {
        requiresAuth: true,
        title: '下单员'
      }
    }, ]
  },
  {
    path: '/',
    component: TheLayout,
    name: '系统设置',
    icon: 'el-icon-star-on',
    menu: true,
    children: [{
        path: '/departmentpage',
        name: '部门',
        component: DepartmentPage,
        meta: {
          requiresAuth: true,
          title: '部门'
        }
      },
      {
        path: '/activationcodepage',
        name: '激活码',
        component: ActivationCodePage,
        meta: {
          requiresAuth: true,
          title: '激活码'
        }
      },
    ]
  },
  {
    path: '/error/401',
    name: '错误401',
    meta: {
      errorPage: true
    },
    component: AppError401
  }, {
    path: '*',
    name: '错误404',
    meta: {
      errorPage: true
    },
    component: AppError404
  }
]

//动态添加的路由


// 页面刷新时，重新赋值token
if (sessionStorage.getItem('token')) {
  store.commit('set_token', sessionStorage.getItem('token'))
}

const router = new Router({
  routes
});

router.beforeEach((to, from, next) => {
  // start progress bar
  NProgress.start()

  if (to.matched.some(r => r.meta.requiresAuth)) { //这里的requireAuth为路由中定义的 meta:{requiresAuth:true}，意思为：该路由添加该字段，表示进入该路由需要登陆的
    if (store.state.token) {
      next();
    } else {
      next({
        path: '/',
      })
    }
  } else {
    next();
  }
});
router.afterEach(() => {
  // finish progress bar
  NProgress.done()
})


export default router



