<template>
  <el-container>
    <el-main v-if="homeindex==true">
      <div class="demo-input-suffix" style="margin-bottom:30px;">
        <template>
          手机号:
          <el-input placeholder="请输入手机号" v-model="phone" clearable style="width:160px;"></el-input>
        </template>
        <template>
          账号状态:
          <el-select
            v-model="yhType"
            placeholder="请选择结算类型"
            @change="selectgetUsersCentergetAllUserfunction()"
          >
            <el-option
              v-for="item in yhTypelist"
              :key="item.id"
              :label="item.name"
              :value="item.id"
            ></el-option>
          </el-select>
        </template>
        <template>
          时间:
          <el-date-picker
            v-model="selecttime"
            type="datetimerange"
            :picker-options="pickerOptions"
            range-separator="至"
            start-placeholder="开始日期"
            end-placeholder="结束日期"
            value-format="yyyy-MM-dd"
            align="center"
          ></el-date-picker>
        </template>

        <el-button
          type="primary"
          icon="el-icon-search"
          @click="selectgetUsersCentergetAllUserfunction()"
        >搜索</el-button>

        <el-button
          type="primary"
          icon="el-icon-refresh"
          circle
          style="float:right;"
          @click="updataall"
        ></el-button>
      </div>
      <el-table
        :data="tableData"
        border
        style="width: 100%"
        v-loading="loading"
        :default-sort="{prop: 'date', order: 'descending'}"
      >
        <el-table-column label="序号" type="index" width="50" align="center"></el-table-column>
        <el-table-column label="手机号" prop="phone" align="center"></el-table-column>
        <el-table-column label="支付宝账号" prop="alipay" align="center"></el-table-column>
        <el-table-column label="真实姓名" prop="realName" align="center"></el-table-column>
        <el-table-column label="绑定平台总数量" prop="totalCount" align="center">
          <template slot-scope="scope">
            <el-popover placement="top" trigger="hover" content="点击可查看绑定平台数量明细。">
              <el-button
                type="text"
                @click="seeMore(scope.row)"
                slot="reference"
              >{{scope.row.totalCount}}</el-button>
            </el-popover>
          </template>
        </el-table-column>
        <el-table-column label="账号状态" prop="userStatus" align="center">
          <template slot-scope="scope">
            <span v-if="scope.row.userStatus==1">已注册</span>
            <span v-if="scope.row.userStatus==2">已绑定</span>
            <span v-if="scope.row.userStatus==3">已冻结</span>
          </template>
        </el-table-column>
        <el-table-column label="注册时间" prop="createTime" align="center" sortable></el-table-column>
        <el-table-column label="操作" align="center" width="200px;">
          <template slot-scope="scope">
            <el-button
              size="small"
              type="danger"
              @click="unbundLing(scope.row.id,scope.row.userStatus)"
              v-if="scope.row.userStatus===1||scope.row.userStatus===2"
            >冻结</el-button>
            <el-button
              size="small"
              type="warning"
              @click="unbundLing(scope.row.id,scope.row.userStatus)"
              v-if="scope.row.userStatus===3"
            >解绑</el-button>
          </template>
        </el-table-column>
      </el-table>

      <!-- 分页 -->
      <div>
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="currentPage"
          :page-sizes="pageSizes"
          :page-size="PageSize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="totalCount"
        ></el-pagination>
      </div>
    </el-main>
    <UserDetailsPage
      :sendid="xquserId"
      :sendids="xquserStatus"
      v-if="showxq==true"
      v-on:listenToChildEvent="showMsgFormChild"
    ></UserDetailsPage>
  </el-container>
</template>


<script>
import DATA from "./userpagejson.js"; //引入静态数据
import UserDetailsPage from "./UserDetailsPage";
import { getUsersCentergetAllUser, postUsersCenterUpdate } from "@/api/api";
export default {
  name: "UserPage",
  inject: ["reload"],
  components: {
    UserDetailsPage
  },

  //组件渲染完毕后调用getUsersCentergetAllUserfunction,来获取所有的用户列表
  mounted: function() {
    //加载数据
    this.tableData = DATA.data.list;
    this.loading = false;

    // this.getUsersCentergetAllUserfunction();
  },
  watch: {},
  data() {
    return {
      xquserId: "",
      xquserStatus: "",
      showxq: false,
      homeindex: true,
      yhType: 1,
      yhTypelist: [
        { id: 1, name: "已注册" },
        { id: 2, name: "已绑定" },
        { id: 3, name: "已冻结" }
      ],
      phone: "",
      loading: true,
      tableData: [],
      // 默认显示第几页
      currentPage: 1,
      // 总条数，根据接口获取数据长度(注意：这里不能为空)
      totalCount: 0,
      // 个数选择器（可修改）
      pageSizes: [15, 20, 30, 40, 50],
      // 默认每页显示的条数（可修改）
      PageSize: 15,
      selecttime: "",
      pickerOptions: {
        shortcuts: [
          {
            text: "最近一周",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit("pick", [start, end]);
            }
          },
          {
            text: "最近一个月",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit("pick", [start, end]);
            }
          },
          {
            text: "最近三个月",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
              picker.$emit("pick", [start, end]);
            }
          }
        ]
      }
    };
  },
  methods: {
    showMsgFormChild(data) {
      //与上面子组件v-on事件名相同
      this.showxq = data; //打印子组件传过来的值
      this.homeindex = true;
    },

    //初始化默认获取表格数据
    getUsersCentergetAllUserfunction: function(insuserStatus) {
      this.tableData = [];
      if (
        (this.selecttime == "" || this.selecttime == null) &&
        (this.phone == "" || this.phone == null)
      ) {
        getUsersCentergetAllUser({
          userStatus: this.yhType,
          pageNum: this.currentPage,
          pageSize: this.PageSize,
          userRole: 1
        })
          .then(res => {
            if (res.code == 200) {
              if (res.data == undefined) {
                this.$message.info("数据为空!");
              } else {
                this.tableData = res.data.list;
                this.totalCount = res.data.total;
                this.$message.success(res.data.msg);
              }
            } else {
              this.$message.info("暂无数据");
            }
          })
          .catch(err => {});
      } else if (
        (this.selecttime == "" || this.selecttime == null) &&
        this.phone != ""
      ) {
        getUsersCentergetAllUser({
          userStatus: this.yhType,
          phone: this.phone,
          pageNum: this.currentPage,
          pageSize: this.PageSize,
          userRole: 1
        })
          .then(res => {
            if (res.code == 200) {
              if (res.data == undefined) {
                this.$message.info("数据为空!");
              } else {
                this.tableData = res.data.list;
                this.totalCount = res.data.total;
                this.$message.success(res.data.msg);
              }
            } else {
              this.$message.info("暂无数据");
            }
          })
          .catch(err => {});
      } else if (this.selecttime != "" && this.phone == "") {
        getUsersCentergetAllUser({
          userStatus: this.yhType,
          pageNum: this.currentPage,
          pageSize: this.PageSize,
          userRole: 1,
          startTime: this.selecttime[0],
          endTime: this.selecttime[1]
        })
          .then(res => {
            if (res.code == 200) {
              if (res.data == undefined) {
                this.$message.info("数据为空!");
              } else {
                this.tableData = res.data.list;
                this.totalCount = res.data.total;
                this.$message.success(res.data.msg);
              }
            } else {
              this.$message.info("暂无数据");
            }
          })
          .catch(err => {});
      } else {
        getUsersCentergetAllUser({
          userStatus: this.yhType,
          phone: this.phone,
          pageNum: this.currentPage,
          pageSize: this.PageSize,
          userRole: 1,
          startTime: this.selecttime[0],
          endTime: this.selecttime[1]
        })
          .then(res => {
            if (res.code == 200) {
              if (res.data == undefined) {
                this.$message.info("数据为空!");
              } else {
                this.tableData = res.data.list;
                this.totalCount = res.data.total;
                this.$message.success(res.data.msg);
              }
            } else {
              this.$message.info("暂无数据");
            }
          })
          .catch(err => {});
      }
      this.loading = false;
    },

    unbundLing(ins, insuserStatus) {
      this.$confirm("确定要修改当前账号的状态吗?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
        center: true
      })
        .then(() => {
          let that = this;
          postUsersCenterUpdate({
            userId: ins,
            userStatus: insuserStatus,
            userRole: 1
          })
            .then(res => {
              if (res.code == 200) {
                that
                  .$confirm(res.msg, "提示", {
                    confirmButtonText: "确定",
                    showClose: false,
                    showCancelButton: false,
                    type: "warning",
                    center: true
                  })
                  .then(() => {
                    that.getUsersCentergetAllUserfunction();
                  });
              } else {
                that.$message.info(res.msg);
              }
            })
            .catch(err => {});
        })
        .catch(() => {
          this.$message.info("已取消");
        });
    },

    selectgetUsersCentergetAllUserfunction: function() {
      this.currentPage = 1;
      this.getUsersCentergetAllUserfunction();
    },

    //分页
    // 每页显示的条数
    handleSizeChange(val) {
      // 改变每页显示的条数
      this.PageSize = val;
      // 注意：在改变每页显示的条数时，要将页码显示到第一页
      this.currentPage = 1;
      this.getUsersCentergetAllUserfunction();
    },
    // 显示第几页
    handleCurrentChange(val) {
      // 改变默认的页数
      this.currentPage = val;
      this.getUsersCentergetAllUserfunction();
    },

    seeMore: function(row) {
      this.showxq = true;
      this.homeindex = false;
      this.xquserId = row.id;
      this.xquserStatus = row.userStatus;
    },

    updataall() {
      this.reload(); //刷新当前页
    }
  }
};
</script>
<style scoped>
</style>


