<template>
  <el-header class="header">
    <router-link to="/">
      <div class="logo" :class="{'logo-hide': !openNav}">
        <img src="../../assets/logo.png" class="image">
        <span class="text" style="color:rgb(85, 26, 139);">后台系统LHC087</span>
      </div>
    </router-link>
    <i
      v-if="!minScreen"
      class="fa fa-align-justify toggle"
      :class="switchbtn?'el-icon-s-fold':'el-icon-s-unfold'"
      style="padding:0 20px;"
      @click="navOpenToggle()"
      title="显示/隐藏菜单"
    ></i>
    <el-dropdown trigger="hover" class="user">
      <span class="user-info">
        {{ user_name }}
        <i class="el-icon-setting"></i>
      </span>
      <el-dropdown-menu slot="dropdown">
        <el-dropdown-item>
          <a style="text-decoration: none;" @click="loginOut()">退出登录</a>
        </el-dropdown-item>
      </el-dropdown-menu>
    </el-dropdown>
  </el-header>
</template>

<script>
export default {
  name: "TheLayoutHeader",
  props: ["openNav"],
  data() {
    let min_screen = window.innerWidth < 768;
    return {
      minScreen: min_screen,
      switchbtn: true,
      user_name: window.sessionStorage.getItem("handleUserName")
    };
  },
  methods: {
    navOpenToggle() {
      this.switchbtn = !this.switchbtn;
      this.$emit("toggle-open");
    },
    loginOut: function() {
      this.$router.push("/");
      this.$store.commit("del_token");
      window.sessionStorage.clear();
    }
  }
};
</script>

<style scoped lang="scss">
.header {
  line-height: 60px;
  background-color: #409eff;
  color: #ffffff;
  div {
    display: inline-block;
  }
  .logo {
    width: 240px;
    border-right: 1px solid #c0c4cc;
    margin-left: -20px;
    text-align: center;
    font-size: 22px;
    cursor: pointer;
    .image {
      width: 40px;
      height: 40px;
      vertical-align: middle;
    }
  }
  .logo-hide {
    width: 65px;
    .text {
      display: none;
    }
  }
  .user {
    float: right;
    cursor: pointer;
    .user-info {
      color: #ffffff;
      i {
        vertical-align: middle;
      }
    }
  }
}
</style>


