import {
  get,
  post,
  del,
  put
}
from '@/utils/request'
//Login页面登录接口
export const postLogin = p => post('/login', p);
//TaskPage页面获取表格数据接口
export const getTaskCenterAll = p => get('/taskCenter/all', p);
//OrderPage页面获取表格数据接口
export const getOrderCenterAll = p => get('/orderCenter/all', p);
//OrderPage页面处理异常信息接口
export const postOrderCenterCompleteToFailure = p => post('/orderCenter/completeToFailure', p);
//OrderPage页面处理异常信息接口
export const postOrderCenteranyToComplete = p => post('/orderCenter/anyToComplete', p);
//OrderPage页面获取记录信息接口
export const getOrderCenterOrderItem = p => get('/orderCenter/orderItem', p);
//OrderPage页面审核不通过接口
export const postOrderCenterAuditFailed = p => post('/orderCenter/auditFailed', p);
//UserPage页面获取表格数据接口和BlogGerPage页面获取表格数据接口（公共，UserPage传1，BlogGerPage传2）
export const getUsersCentergetAllUser = p => get('/usersCenter/getAllUser', p);
//UserPage页面口和BlogGerPage页面编辑账号冻结和解绑状态接口（公共，UserPage传1，BlogGerPage传2）
export const postUsersCenterUpdate = p => post('/usersCenter/update', p);
//UserPage页面获取表格数据接口
export const getUsersCentergetUserPlatform = p => get('/usersCenter/getUserPlatform', p);
//BlogGerPage页面获取部门接口（公共）
export const getDeptallTotal = p => get('/dept/allTotal', p);
//BlogGerPage页面编辑账号部门接口
export const postUsersCenterUpdateBozhuDept = p => post('/usersCenter/updateBozhuDept', p);
//SettlementPage页面获取表格数据接口
export const getPayCenterPayList = p => get('/payCenter/payList', p);
//SettlementPage页面结算操作接口
export const postPayCenterSettlement = p => post('/payCenter/settlement', p);
//SettlementDetailsPage页面获取表格数据接口
export const postPayCenterPayDetail = p => post('/payCenter/payDetail', p);
//DepartmentPage页面获取表单数据接口
export const getDeptAll = p => get('/dept/all', p);
//DepartmentPage页面添加部门接口
export const postDeptInsert = p => post('/dept/insert', p);
//DepartmentPage页面修改部门接口
export const postDeptUpdate = p => post('/dept/update', p);
//DepartmentPage页面删除部门接口
export const delDeptdelete = p => del('/dept/delete', p);
//ActivationCodePage页面获取表单数据接口
export const getCodeSend = p => get('/code/send', p);



