<template>
  <aside class="sidebar" id="aslide" :class="{'sidebar-hide': !openNav}">
    <vue-scroll>
      <el-menu
        :default-active="$route.path"
        class="sidebar-menu"
        :collapse="!openNav"
        @select="menuSelect"
        :collapse-transition="false"
      >
        <template v-for="(level1, index1) in activeUsers">
          <!-- 一级菜单 -->
          <el-menu-item
            v-if="level1.children.length === 1"
            :index="level1.children[0].path"
            :key="index1"
          >
            <i class="el-icon-menu"></i>
            <span slot="title">{{level1.children[0].name}}</span>
          </el-menu-item>
          <el-submenu :index="index1+''" v-if="level1.children.length > 1" :key="index1+''">
            <template slot="title">
              <i :class="level1.icon"></i>
              <span slot="title">{{level1.name}}</span>
            </template>
            <template v-for="(level2, index2) in level1.children">
              <!-- 二级菜单 -->
              <el-menu-item
                v-if="!level2.children"
                :index="level2.path"
                :key="index1+'-'+index2"
                :class="level2.class"
              >
                <i :class="level2.icon"></i>
                <span slot="title">{{level2.name}}</span>
              </el-menu-item>
              <el-submenu
                :index="index1+'-'+index2"
                v-if="level2.children"
                :key="index1+'-'+index2"
              >
                <!-- 三级菜单 -->
                <template slot="title">
                  <i :class="level2.icon"></i>
                  {{level2.name}}
                </template>
                <el-menu-item
                  v-for="(level3, index3) in level2.children"
                  :index="level3.path"
                  :key="index1+'-'+index2+'-'+index3"
                >
                  <i :class="level3.icon"></i>
                  <span slot="title">{{level3.name}}</span>
                </el-menu-item>
              </el-submenu>
            </template>
          </el-submenu>
        </template>
      </el-menu>
    </vue-scroll>
  </aside>
</template>

<script>
export default {
  name: "TheLayoutSidebar",
  props: ["openNav"],
  inject: ["reload"],
  data() {
    return {
      RoleAuthorization: window.sessionStorage.getItem("roleauthorization"),
      newrouter: [
        {
          path: "/",
          component: "TheLayout",
          icon: "el-icon-s-home",
          menu: true,
          children: [
            {
              path: "/taskpage",
              name: "任务中心",
              component: "TaskPage",
              meta: {
                requiresAuth: true,
                title: "任务中心"
              }
            }
          ]
        },
        {
          path: "/",
          component: "TheLayout",
          icon: "el-icon-star-on",
          menu: true,
          children: [
            {
              path: "/orderpage",
              name: "订单中心",
              component: "OrderPage",
              meta: {
                requiresAuth: true,
                title: "订单中心"
              }
            }
          ]
        }
      ]
    };
  },
  computed: {
    activeUsers: function() {
      if (this.RoleAuthorization == 1) {
        return this.newrouter;
      } else if (this.RoleAuthorization == 0) {
        return this.$router.options.routes.filter(function(level1) {
          return level1.menu;
        });
      }
    }
  },
  // 监听,当路由发生变化的时候执行
  watch: {
    $route(to, from) {
      //this.updataall();
    }
  },
  methods: {
    menuSelect(index) {
      this.$router.push(index);
      this.updataall();
    },

    updataall() {
      this.reload(); //刷新当前页
    }
  }
};
</script>

<style scoped lang="scss">
.sidebar {
  width: 240px;
  position: absolute;
  top: 0;
  bottom: 0;
  border-right: 1px solid #e6e6e6;
  .sidebar-menu {
    border: none;
    height: 100%;
  }
}
.hideweibo {
  display: none;
}
.redbook {
  display: none;
}
.sidebar-hide {
  width: 65px;
}
</style>


