<template>
  <el-container>
    <el-main>
      <div class="demo-input-suffix" style="margin-bottom:30px;">
        <el-button
          type="primary"
          icon="el-icon-back"
          style="float:right;margin-bottom:30px;"
          @click="callback"
        >返回</el-button>
      </div>
      <el-table
        :data="tableData"
        border
        style="width: 100%"
        v-loading="loading"
        :default-sort="{prop: 'date', order: 'descending'}"
      >
        <el-table-column label="序号" type="index" width="50" align="center"></el-table-column>
        <el-table-column label="平台" prop="platformType" align="center">
          <template slot-scope="scope">
            <span v-if="scope.row.platformType==1">微博</span>
          </template>
        </el-table-column>
        <el-table-column label="平台标识(UID)" prop="accountUid" align="center"></el-table-column>
        <el-table-column label="昵称" prop="accountNickName" align="center"></el-table-column>
        <el-table-column label="绑定时间" prop="createTime" align="center" sortable></el-table-column>
      </el-table>
    </el-main>
  </el-container>
</template>

<script>
import DATA from "./userdetailspagejson.js"; //引入静态数据
import { getUsersCentergetUserPlatform } from "@/api/api";
export default {
  name: "UserDetailsPage",
  props: ["sendid", "sendids"],
  //组件渲染完毕后调用getUsersCentergetUserPlatformfunction,来获取所有的用户列表
  mounted: function() {
    //加载数据
    this.tableData = DATA.data;
    this.loading = false;
    // this.getUsersCentergetUserPlatformfunction();
  },

  data() {
    return {
      loading: true,
      tableData: []
    };
  },
  methods: {
    //初始化默认获取表格数据
    getUsersCentergetUserPlatformfunction: function() {
      getUsersCentergetUserPlatform({
        userId: this.sendid,
        userStatus: this.sendids
      })
        .then(res => {
          if (res.code == 200) {
            if (res.data == undefined) {
              this.$message.info("数据为空!");
            } else {
              this.tableData = res.data;
              this.$message.success(res.msg);
            }
          } else {
            this.$message.info("暂无数据");
          }
          this.loading = false;
        })
        .catch(err => {});
    },
    callback() {
      this.$emit("listenToChildEvent", "false");
    }
  }
};
</script>
<style scoped>
.name-wrapper {
  cursor: pointer;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.buttonText {
  text-decoration: none;
  cursor: pointer;
  color: #409eff !important;
}
</style>


