<template>
  <el-container>
    <el-main>
      <div class="demo-input-suffix" style="margin-bottom:30px;">
        <el-button type="primary" icon="el-icon-plus" @click="addDialog = true">添加</el-button>
        <el-button
          type="primary"
          icon="el-icon-refresh"
          circle
          style="float:right;"
          @click="updataall"
        ></el-button>
      </div>
      <el-table :data="tableData" border style="width: 100%" v-loading="loading">
        <el-table-column label="序号" type="index" width="50" align="center"></el-table-column>
        <el-table-column label="部门" prop="deptName" align="center"></el-table-column>
        <el-table-column label="操作" align="center" width="180px;">
          <template slot-scope="scope">
            <el-button size="small" type="primary" @click="setUser(scope.row)">编辑</el-button>
            <el-button size="small" type="danger" @click="deleteUser(scope.row)">删除</el-button>
          </template>
        </el-table-column>
      </el-table>

      <!-- 分页 -->
      <div>
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="currentPage"
          :page-sizes="pageSizes"
          :page-size="PageSize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="totalCount"
        ></el-pagination>
      </div>
    </el-main>
    <el-dialog
      title="添加部门"
      :visible.sync="addDialog"
      @close="resetForm('addForm')"
      align="center"
      :close-on-click-modal="false"
    >
      <el-form :model="addForm" :rules="addRules" ref="addForm" label-width="120px">
        <el-form-item label="部门" prop="adddepartment">
          <el-input type="text" v-model="addForm.adddepartment" clearable></el-input>
        </el-form-item>

        <el-button type="primary" @click.stop="submitForm('addForm')">提交</el-button>
      </el-form>
    </el-dialog>
    <el-dialog
      title="修改部门"
      :visible.sync="editDialog"
      @close="resetForm('editForm')"
      align="center"
      :close-on-click-modal="false"
    >
      <el-form :model="editForm" :rules="editRules" ref="editForm" label-width="120px">
        <el-form-item label="序号" prop="id">
          <el-input type="text" v-model.number="editForm.id" :disabled="true"></el-input>
        </el-form-item>
        <el-form-item label="部门" prop="editdepartment">
          <el-input type="text" v-model="editForm.editdepartment" clearable></el-input>
        </el-form-item>
        <el-button type="primary" @click.stop="updateUser('editForm')">修改</el-button>
      </el-form>
    </el-dialog>
  </el-container>
</template>

<script>
import DATA from "./departmentpagejson.js"; //引入静态数据
import {
  getDeptAll,
  postDeptInsert,
  postDeptUpdate,
  delDeptdelete
} from "@/api/api";
export default {
  name: "DepartmentPage",
  inject: ["reload"],

  //组件渲染完毕后调用getDeptAllfunction,来获取所有的用户列表
  mounted: function() {
    //加载数据
    this.tableData = DATA.data.list;
    this.loading = false;

    // this.getDeptAllfunction();
  },
  watch: {},
  data() {
    return {
      loading: true,
      tableData: [],
      // 默认显示第几页
      currentPage: 1,
      // 总条数，根据接口获取数据长度(注意：这里不能为空)
      totalCount: 0,
      // 个数选择器（可修改）
      pageSizes: [15, 20, 30, 40, 50],
      // 默认每页显示的条数（可修改）
      PageSize: 15,

      //删除的对话框
      addDialog: false,
      //编辑的对话框
      editDialog: false,
      addForm: {
        adddepartment: ""
      },
      addRules: {
        adddepartment: [
          { required: true, message: "部门不能为空", trigger: "blur" }
        ]
      },
      //编辑表单
      editForm: {
        id: "",
        editdepartment: ""
      },
      editRules: {
        editdepartment: [
          { required: true, message: "部门不能为空", trigger: "blur" }
        ]
      }
    };
  },
  methods: {
    //初始化默认获取表格数据
    getDeptAllfunction: function(inspayStatus) {
      this.tableData = [];
      getDeptAll({
        pageNum: this.currentPage,
        pageSize: this.PageSize
      })
        .then(res => {
          if (res.code == 200) {
            if (res.data.list.length == 0) {
              this.$message.info("数据为空!");
              this.tableData = res.data.list;
              this.totalCount = res.data.total;
            } else {
              this.tableData = res.data.list;
              this.totalCount = res.data.total;
              this.$message.success(res.msg);
            }
          } else {
            this.$message.error(res.msg);
          }
          this.loading = false;
        })
        .catch(err => {});
    },

    handleSizeChange(val) {
      // 改变每页显示的条数
      this.PageSize = val;
      // 注意：在改变每页显示的条数时，要将页码显示到第一页
      this.currentPage = 1;
      this.getDeptAllfunction();
    },
    // 显示第几页
    handleCurrentChange(val) {
      // 改变默认的页数
      this.currentPage = val;
      this.getDeptAllfunction();
    },

    //表单提交
    submitForm: function(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          postDeptInsert({
            deptName: this.addForm.adddepartment
          })
            .then(res => {
              if (res.code == 200) {
                this.$message.success(res.msg);
                this.getDeptAllfunction();
                this.resetForm(formName);
              } else {
                this.$message.info(res.msg);
              }
            })
            .catch(err => {});
        } else {
          return false;
        }
      });
    },
    //修改表单赋值
    setUser: function(row) {
      //编辑的弹出框开启
      this.editDialog = true;
      //this.editForm = Object.assign({}, row);
      this.editForm.id = row.id;
      this.editForm.editdepartment = row.deptName;
    },

    updateUser: function(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          postDeptUpdate({
            deptId: this.editForm.id,
            deptName: this.editForm.editdepartment
          })
            .then(res => {
              if (res.code == 200) {
                this.$message.success(res.msg);
                this.getDeptAllfunction();
                this.resetForm(formName);
              } else {
                this.$message.info(res.msg);
              }
            })
            .catch(err => {});
        } else {
          return false;
        }
      });
    },

    //删除信息
    deleteUser: function(row) {
      this.$confirm("此操作将永久删除这条数据, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
        center: true
      })
        .then(() => {
          delDeptdelete({
            deptId: row.id
          })
            .then(res => {
              if (res.code == 200) {
                this.$message.success(res.msg);
                this.getDeptAllfunction();
              } else {
                this.$message.info(res.msg);
              }
            })
            .catch(err => {});
        })
        .catch(() => {
          this.$message.info("已取消删除");
        });
    },
    resetForm: function(formName) {
      if (formName == "addForm") {
        this.$refs["addForm"].resetFields();
        //将新增的弹出框关闭
        this.addDialog = false;
      } else if (formName == "editForm") {
        this.$refs["editForm"].resetFields();
        //编辑的弹出框关掉
        this.editDialog = false;
      }
    },
    updataall() {
      this.reload(); //刷新当前页
    }
  }
};
</script>
<style scoped>
</style>


