<template>
  <el-container>
    <el-main>
      <div class="demo-input-suffix" style="margin-bottom:30px;">
        <template>
          激活码数量:
          <el-input placeholder="输入生成激活码数量" v-model="totalnumber" clearable style="width:20%;"></el-input>
        </template>

        <el-button type="primary" icon="el-icon-zoom-in" @click="makejhm()">生成激活码</el-button>
        <el-button
          type="primary"
          icon="el-icon-refresh"
          circle
          style="float:right;"
          @click="updataall"
        ></el-button>
      </div>
      <el-table
        :data="tableData.slice((currentPage-1)*PageSize,currentPage*PageSize)"
        border
        style="width: 100%"
        v-loading="loading"
      >
        <el-table-column label="序号" type="index" width="50" align="center"></el-table-column>
        <el-table-column label="激活码" prop="codeNum" align="center"></el-table-column>
      </el-table>

      <!-- 分页 -->
      <div>
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="currentPage"
          :page-sizes="pageSizes"
          :page-size="PageSize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="totalCount"
        ></el-pagination>
      </div>
    </el-main>
  </el-container>
</template>


<script>
import DATA from "./activationcodepagejson.js"; //引入静态数据
import { getCodeSend } from "@/api/api";
export default {
  name: "ActivationCodePage",
  inject: ["reload"],

  //组件渲染完毕后调用getCodeSendfunction,来获取所有的用户列表
  mounted: function() {
    //加载数据
    this.tableData = DATA.data;
    this.loading = false;
    // this.getCodeSendfunction();
  },
  watch: {},
  data() {
    return {
      loading: true,
      totalnumber: 15,
      tableData: [],
      // 默认显示第几页
      currentPage: 1,
      // 总条数，根据接口获取数据长度(注意：这里不能为空)
      totalCount: 0,
      // 个数选择器（可修改）
      pageSizes: [15, 20, 30, 40, 50],
      // 默认每页显示的条数（可修改）
      PageSize: 15
    };
  },
  methods: {
    //初始化默认获取表格数据
    getCodeSendfunction: function() {
      this.tableData = [];
      getCodeSend({
        total: this.totalnumber
      })
        .then(res => {
          if (res.code == 200) {
            if (res.data == undefined) {
              this.$message.info("数据为空!");
            } else {
              this.tableData = res.data;
              this.totalCount = res.data.length;
              this.$message.success(res.msg);
            }
          } else {
            this.$message.error(res.msg);
          }
          this.loading = false;
        })
        .catch(err => {});
    },
    makejhm: function() {
      if (
        (this.totalnumber <= 0) | (this.totalnumber == "") ||
        this.totalnumber == null
      ) {
        this.$message.info("请正确输入激活码数量");
      } else {
        this.currentPage = 1;
        this.getCodeSendfunction();
      }
    },
    handleSizeChange(val) {
      // 改变每页显示的条数
      this.PageSize = val;
      // 注意：在改变每页显示的条数时，要将页码显示到第一页
      this.currentPage = 1;
    },
    // 显示第几页
    handleCurrentChange(val) {
      // 改变默认的页数
      this.currentPage = val;
    },
    updataall() {
      this.reload(); //刷新当前页
    }
  }
};
</script>
<style scoped>
</style>


